<template>
  <section class="main-banner-section">
    <top></top>
    <div class="banner-wrap clearfix">
      <div class="map-box fl">
        <echarts-map :color="'#fff'" :height="'300px'" :width="'400px'" @changeMapData="changeMapData"></echarts-map>
      </div>
      <section class="map-label fr" v-if="false">
        <ul class="legends-box">
          <li class="lengend-item">
            <h4><span class="lengend-color red"></span>学前教育领跑者</h4>
            <p class="mar-top">静安区(20%)</p>
            <p>徐汇区(20%)</p>
            <p>杨浦区(20%)</p>
          </li>
          <li class="lengend-item">
            <h4><span class="lengend-color yellow"></span>学前教育加速者</h4>
            <p class="mar-top">黄浦区(67%)</p>
            <p>虹口区(67%)</p>
            <p>闵行区(67%)</p>
            <p>浦东区(67%)</p>
            <p>松江区(67%)</p>
          </li>
          <li class="lengend-item">
            <h4><span class="lengend-color green"></span>学前教育起跑者</h4>
            <p class="mar-top">长宁区(90%)</p>
            <p>普陀区(90%)</p>
            <p>宝山区(90%)</p>
            <p>嘉定区(90%)</p>
            <p>金山区(90%)</p>
            <p>青浦区(90%)</p>
            <p>奉贤区(90%)</p>
          </li>
        </ul>
      </section>
    </div>
  </section>
</template>

<script>
  import top from '../../../components/home-top'
  import echartsMap from './echarts-map'
  import areaJson from '@/mock/areaJson'
  export default {
    components: {
      echartsMap,
      top
    },
    data() {
      return {
        areaJoson: areaJson,
        mapData: {
          name: '',
          value: []
        }
      }
    },
    methods: {
      changeMapData(data) {
        this.mapData = data;
      }
    }
  }
</script>

<style scoped lang="scss">
  .main-banner-section {
    height: 619px;
    background: url('../../../assets/image/banner-bg.png') top center no-repeat;
    position: relative;
    .banner-wrap {
      width: 1200px;
      height: 400px;
      margin: 50px auto 0;
      overflow: hidden;
    }
    .map-box {
      width: 400px;
      height: 300px;
      margin-top: 50px;
    }
    .map-label {
      margin-top: 100px;
      font-size: 24px;
      color: #fff;
      .legends-box {
        width: 580px;
        display: flex;
        .lengend-item {
          font-size: 14px;
          color: #fff;
          margin-bottom: 28px;
          width: 33%;
          h4,
          p {
            height: 20px;
            line-height: 20px;
          }
          p {
            text-indent: 2em;
          }
        }
        .lengend-color {
          display: inline-block;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          margin-right: 10px;
          position: relative;
          top: 50%;
          transform: translateY(-50%);
        }
        .green {
          background-color: #e5d692;
        }
        .yellow {
          background-color: #f6c138;
        }
        .red {
          background-color: #f7a02b;
        }
        .area-item {
          float: left;
          margin-left: 10px;
        }
      }
      span {
        margin: 0 5px;
      }
      .map-label-value {
        font-weight: 700;
      }
    }
  }
  .mar-top{
    margin-top:6px;
  }
</style>



