<template>
    <div id="echartsBox" :style="{width: width, height: height}">
    </div>
</template>

<script>
    import areaJson from '@/libs/citys.js';
    export default {
        mounted() {
            this.initMap();
        },
        props: [
            'color', 'width', 'height'
        ],
        data() {
            return {
                year: 2016,
                oA: [
                    0, 55, 65, 100
                ],
                nA: [40, 60, 80, 100],
                option: {
                    tooltip: {
                        trigger: 'item',
                        formatter: (params)=> {
                            if(params.data.name==='杨浦区'){
                                params.data.value[2] = 90;
                                this.$emit('changeMapData',{name:'杨浦区',value:params.data.value})
                            }else{
                                this.$emit('changeMapData',params.data)
                            }
                            return params.name + ' : ' + params.data.value[2];
                        }
                    },
                    visualMap: {
                        min: 10,
                        max: 130,
                        calculable: true,
                        inRange: {
                            color: ['#f1f414', '#0eec18', '#f30829']
                        },
                        textStyle: {
                            color: '#000'
                        }
                    },
                    geo: {
                        show: false,
                        map: 'shanghai',
                        label: {
                            normal: {
                                show: false
                            },
                            emphasis: {
                                show: true,
                            }
                        },
                        roam: true,
                        itemStyle: {
                            normal: {
                                borderColor: "#555"
                            },
                            emphasis: {
                                areaColor: '#fd007f'
                            }
                        }
                    },
                    series: [{
                        name: '上海学前教育指标体系',
                        roam: false,
                        type: 'map',
                        map: 'shanghai',
                        itemStyle: {
                            normal: {
                                show: false,
                                borderColor: "#555"
                            },
                            emphasis: {
                                show: true,
                                areaColor: '#fd007f'
                            }
                        },
                        label: {
                            normal: {
                                show: false,
                                textStyle: {
                                    color: '#000'
                                }
                            },
                            emphasis: {
                                show: true
                            }
                        },
                        data: []
                    }]
                }
            }
        },
        methods: {
            initCitys() {
                const obj = {};
                areaJson.forEach(item => {
                    const key = item.county;
                    obj[key] = [item.latitude, item.longitude]
                })
                return obj;
            },
            convertData(geoCoordMap, data) {
                var res = [];
                for (var i = 0; i < data.length; i++) {
                    var geoCoord = geoCoordMap[data[i].name];
                    if (geoCoord) {
                        res.push({
                            name: data[i].name,
                            value: geoCoord.concat(data[i].value)
                        });
                    }
                }
                return res;
            },
            async initMap() {
                const _aj = this.initCitys();
                var _d = areaJson.map(item => {
                    return {
                        name: item.county,
                        value: item.value
                    }
                })
                this.option.series[0].data = this.convertData(_aj, _d);
                const myChart = this.$echarts.init(document.getElementById('echartsBox'));
                myChart.on('click', (params) => {
                    this.$store.commit('setArea',params.data.name);
                    this.$router.push({
                        name: 'generate',
                        query: {
                            area:params.data.name
                        }
                    })
                });
                const geoJson = await require('../../../mock/shanghai.json');
                this.$echarts.registerMap('shanghai', geoJson);
                myChart.setOption(this.option);
            }
        }
    }
</script>

<style>

</style>


