<template>
  <div class="main-legal-section">
    <div class="g-in">
      <ul class="nav-box">
        <li :style="{backgroundImage: 'url('+require('../../../assets/image/data-section-img'+item+'.png')}"
          v-for="item in 4" :key="item" @click="godetail(item)"></li>
      </ul>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      list: ['generate','caselist','lawlist','about']
    }
  },
  methods: {
    // 跳转
    godetail(item) {
      // console.log(this.list[item-1])
      this.$router.push({
        name: this.list[item-1]
      })
    }
  }
}
</script>

<style lang="scss">
.main-legal-section{
  height: 500px;
  background: url('../../../assets/image/section-bg2.png') no-repeat 0 -37px;
  background-color: #fff;
  box-sizing: border-box;
  padding: 20px 0;
  .nav-box{
    margin-top: 80px;
    li{
      display: inline-block;
      width: 280px;
      height: 224px;
      background-repeat: no-repeat;
      background-size: cover;
      margin-left: 25px;
      box-shadow: 0 0 10px #ccc;
      transition: transform .5s;
      cursor: pointer;
      &:first-of-type{
        margin-left: 0;
      }
      &:hover{
        transform: translateY(-30px);
      }
    }
  }
}
</style>



