<template>
  <div class="main-slider">
    <div class="g-in">
      <div class="title-img"></div>
      <div class="my-flipster clearfix">
        <ul class="topic-home-left fl">
          <li><img src="@/assets/image/slide-section-main-left-top.png" alt=""></li>
          <li><img src="@/assets/image/slide-section-main-left-bottom.png" alt=""></li>
        </ul>
        <ul class="topic-home-right fr">
          <li><img src="@/assets/image/slide-section-main-right-top.png" alt=""></li>
          <li><img src="@/assets/image/slide-section-main-right-center.png" alt=""></li>
          <li><img src="@/assets/image/slide-section-main-right-bottom.png" alt=""></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  data() {
    return {
      pageInfo: {
        pageNumber: 1,
        pageSize: 10
      },
      lawList: []
    }
  },
  created() {
  },
  methods: {
   
  }
}
</script>

<style lang="scss">
.main-slider{
  height: 730px;
  background: url('../../../assets/image/slide-section-bg.png') no-repeat;
  background-size: cover;
  box-sizing: border-box;
  padding: 40px 0;
  .title-img{
    width: 203px;
    height: 52px;
    background: url('../../../assets/image/slide-section-title.png') no-repeat;
    background-size: cover;
  }
  .my-flipster{
    margin-top: 40px;
    .topic-home-left{
      width: 700px;
      height: 520px;
      li{
        width: 100%;
        height: 260px;
        overflow: hidden;
        position: relative;
        cursor: pointer;
        &:hover img{
          transform: translate(-50%, -50%) scale(1.1);
        }
        img{
          height: 100%;
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          transition: all 0.6s;
        }
      }
    }
    .topic-home-right{
      width: 490px;
      height: 520px;
      li{
        width: 100%;
        height: 173.3px;
        overflow: hidden;
        position: relative;
        cursor: pointer;
        &:hover img{
          transform: translate(-50%, -50%) scale(1.1);
        }
        img{
          width: 100%;
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          transition: all 0.6s;
        }
      }
    }
  }
}
</style>



