<template>
  <div class="main">
    <Top :menuList="menuList"></Top>
    <router-view />
    <Bottom></Bottom>
  </div>
</template>

<script>
  import Top from '../components/top'
  import Bottom from '../components/bottom'
  export default {
    components: {
      Top,
      Bottom
    },
    data() {
      return {
        menuList: this.$store.state.app.menuList
      }
    },
    created() {}
  }
</script>



