<template>
  <div class="top-wrap1" v-if="$route.path !== '/homepage'">
    <nav class="top-nav g-in clearfix">
      <!-- logo -->
      <div class="top-logo fl">
        <img v-if="$route.path === '/homepage'" src="@/assets/image/logoHome.png" @click="$router.push('/')">
        <img v-else src="@/assets/image/logo.png" @click="$router.push('/')">
      </div>
      <!-- nav导航 -->
      <ul class="top-nav-list fr">
        <li :class="[$route.path.includes(item.path)?'active':'']" v-for="(item, index) in menuList" :key="index" @click="goDetail(item)">{{ item.meta.title }}</li>
        <!-- 登录 -->
        <div class="top-admin fr">
          <Button v-if="!userName" type="primary" shape="circle" size="small" @click="login">登 录</Button>
          <Poptip v-else class="avatar-wrap">
            <div style="cursor: pointer; height:40px; line-height: 40px;">
              <span class="name-wrap">{{userName}}</span>
            </div>
            <div slot="content" style="text-align: center;">
              <Button type="text" @click="gotoInfo" class="login-menu">个人中心</Button>
              <Button type="text" @click="logout" class="login-menu">退出登录</Button>
            </div>
          </Poptip>
        </div>
      </ul>
    </nav>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
export default {
  props: ['menuList'],
  data() {
    return {
      userName: ''
    }
  },
  created() {
    this.userName = Cookies.get('Client-User')
  },
  methods: {
    // 去对应页面
    goDetail(item) {
      // console.log(item)
      this.$router.push({
        name: item.name
      })
    },
    // 登录
    login() {
      this.$router.push({
        name: 'login'
      })
    },
    // 个人中心
    gotoInfo() {
      this.$router.push({
        name: 'userinfo'
      })
    },
    // 登出
    async logout() {
      const res = await this.$ajaxPost(this.projectName + '/account/logout.do')
      this.$Message.success('恭喜您退出成功!')
      Cookies.remove('Client-User')
      Cookies.remove('Uu-id')
      // Cookies.remove('case')
      // Cookies.remove('chartInfo')
      this.$router.replace({
        path: '/loading'
      })
    }
  }
}
</script>

<style lang="scss">
.top-wrap1{
  width: 100%;
  background-color: #fff;
  .top-nav{
    height: 120px;
    .top-logo{
      position: relative;
      height: 120px;
      img{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
      }
    }
    .top-nav-list{
      height: 100%;
      line-height: 120px;
      color: #7e8c8d;
      li{
        cursor: pointer;
        display: inline-block;
        margin-right: 30px;
        font-size: 15px;
      }
      .active{
        font-weight: 700; 
        font-size: 15px;
      }
      .top-admin{
        height: 100%;
        line-height: 120px;
        margin-left: 60px;
        .ivu-btn-primary{
          background-color: #00aec4;
          border-color: #00aec4;
          padding: 2px 12px;
        }
        .avatar-wrap{
          .login-menu{
            box-shadow: none;
            border: none;
            display: block;
            width: 100%;
          }
        }
      }
    }
  }
}
</style>



