<template>
  <div class="main-news">
    <div class="g-in">
      <div class="title-img clearfix">
        <div class="fl"></div>
        <Button class="fr" type="dashed" size="large" @click="goNewsList">
          更多
          <Icon type="ios-arrow-forward" /></Button>
      </div>
      <div class="news-list">
        <div class="news-home" @click="godetail(newsInfo.id)">
          <div class="news-img fl">
            <img :src="newsInfo.titleImageUrl?newsInfo.titleImageUrl:''" alt="新闻缩略图">
          </div>
          <div class="news-info fl">
            <p>{{titleLength>34?newsInfo.title.substring(0, 34)+'...':newsInfo.title}}</p>
            <div>
              <p>{{newsInfo.author}}</p>
              <p>{{newsInfo.publishTime&&newsInfo.publishTime.split(' ')[0]}}</p>
            </div>
          </div>
          <img class="go-img fl" src="@/assets/image/arrow.png">
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      pageInfo: {
        pageNumber: 1,
        pageSize: 10
      },
      newsInfo: {},
      code: 1000,
      titleLength: 0
    }
  },
  created() {
    this.getNewsAll()
    this.getNewsInfo()
  },
  methods: {
    async getNewsAll() {
      const res = await this.$ajaxPost(this.projectName + '/news/findAll.do')
      this.code = res[0].classesId
    },
    async getNewsInfo() {
      const res = await this.$ajaxPost(this.projectName + '/news/finds.do', {
        classesId: this.code,
        ...this.pageInfo,
        publishFlag: '1'
      })
      const list = res.rows
      this.newsInfo = list[0]
      this.titleLength = this.newsInfo.title.length
    },
    goNewsList() {
      this.$router.push({
        name: 'newslist'
      })
    },
    godetail(id) {
      this.$router.push({
        name: 'newsdetail',
        params: {
          id
        }
      })
    }
  }
}
</script>


<style lang="scss">
.main-news{
  background-color: #fff;
  height: 600px;
  box-sizing: border-box;
  padding: 40px 0;
  .title-img{
    >div{
      width: 183px;
      height: 52px;
      background: url('../../../assets/image/news-title.png') no-repeat;
      background-size: cover;
    }
    >button{
      margin-top: 8px;
    }
  }
  .news-list{
    margin-top: 40px;
    .news-home{
      border: 1px solid #cfcfcf;
      height: 420px;
      box-sizing: border-box;
      padding: 10px;
      cursor: pointer;
      position: relative;
      .news-img {
        width: 710px;
        height: 398px;
        overflow: hidden;
        position: relative;
        img {
          display: block;
          width: 100%;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
      .news-info {
        width: 400px;
        height: 100%;
        box-sizing: border-box;
        padding: 40px;
        position: relative;
        >p{
          font-size: 26px;
          color: #000;
        }
        >div{
          position: absolute;
          bottom: 40px;
          p{
            color: #666;
            line-height: 30px;
          }
        }
      }
      .go-img{
        width: 40px;
        position: absolute;
        bottom: 60px;
      }
    }
  }
}
</style>



