<template>
  <footer class="bottom-wrap">
    <div class="g-in footer-content">
        <p>关于我们 &nbsp;|&nbsp; 隐私声明</p>
        <p>Copyright&nbsp; {{ date }} 上海学前教育发展监测平台</p>
    </div>
</footer>
</template>

<script>
export default {
  data () {
    return {
      date: new Date().getFullYear()
    }
  }
}
</script>

<style scoped>
  .bottom-wrap {
    height: 100px;
    background: #00aec4;
    color: #fff;
    overflow: hidden;
  }
  .footer-content {
    letter-spacing: 1px;
    overflow: hidden;
    margin-top: 20px;
    line-height: 30px;
    text-align: center;
  }
</style>



